.modal_overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.2);
  visibility: hidden;
  z-index: -1;
  opacity: 0;
  transition-property: opacity visibility;
  transition-duration: 500ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 1;
  &.active {
    visibility: visible;
    z-index: 10;
    opacity: 1;
  }
  .modal {
    background-color: #fff;
    color: #000;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-weight: 500;
    padding: 0;
  }
}
