// BUTTON
.form__btn-list {
  display: flex;
  justify-content: flex-end;
}
.form-button {
  text-align: center;
  width: 90px;
  height: 30px;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #ffffff;
  border: 1px solid #000000;
  border-radius: 5px;
  transition: color 0.25s cubic-bezier(0.4, 0, 0.2, 1),
    background-color 0.25s cubic-bezier(0.4, 0, 0.2, 1),
    box-shadow 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  @media screen and (min-width: 768px) {
    width: 125px;
  }
  @media screen and (min-width: 1280px) {
    width: 136px;
  }
}
.form-button:not(:last-child) {
  margin-right: 15px;
}

.form-btn {
  color: #fff;
  box-shadow: none;
  cursor: auto;
  background-color: #4ec9dc;
  border: 1px solid #208b9b;
}

.form-btn:hover,
.form-btn:focus,
.remove-queue-btn {
  color: #208b9b;
  cursor: pointer;
  background-color: #fff;
  // border: 2px solid #d05300;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
    rgba(0, 0, 0, 0.22) 0px 15px 12px;
}

// BUTTON-CLOSE
.btn-close {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 8px;
  right: 8px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: #ffffff;
  border: 0px solid rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  cursor: pointer;
}
.form__close-icon {
  transform: rotate(-90deg);
}
.btn-close:hover,
.btn-close:focus {
  // border: 2px solid #ff6b08;
  fill: #ff6b08;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
    rgba(0, 0, 0, 0.22) 0px 15px 12px;
}

// IMG
.form-img {
  width: 240px;
  height: 357px;
  object-fit: cover;
  border-radius: 5px;
  margin-left: auto;
  margin-right: auto;
  display: block;

  @media screen and (min-width: 768px) {
    width: 264px;
    height: 374px;
  }
  @media screen and (min-width: 1280px) {
    height: 478px;
    width: 100%;
  }
}
body.modal.active {
  height: 100vh;
  overflow-y: hidden;
}
