.modal_overlay {
  width: 100%;
  height: 100%;
  visibility: hidden;
  z-index: -1;
  opacity: 0;
  transition-property: opacity visibility;
  z-index: 1;
  background: #0003;
  transition-duration: .5s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.modal_overlay.active {
  visibility: visible;
  z-index: 10;
  opacity: 1;
}

.modal_overlay .modal {
  color: #000;
  background-color: #fff;
  padding: 0;
  font-weight: 500;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

